.Replacer-container {
    display: grid;
    grid-template-columns: 60% 40%;
}

.Replacer-text {
    grid-column: 1;
    grid-row: 1;

}
.Replacer-text > textarea {
    width: 58vw;
    height: 250px;
}
.Replacer-text > .Replacer-replace {
    background-color: #004400;
    color: white;
    width: 150px;
    margin: 8px auto;
    padding: 4px;
    border-radius: 2px;
    cursor: pointer;
}
.Replacer-text > .Replacer-replace:hover {
    background-color: #007700;
}


.Replacer-rules {
    grid-column: 2;
    grid-row: 1;
    border-left: 1px solid gray;
}

.Replacer-upload-rulesets {
    background-color: lightskyblue;
    margin: 4px;
    border-radius: 2px;
}
.Replacer-upload-rulesets > input {
    background-color: lightskyblue;
    display: inline-block;
}
.Replacer-upload-rulesets > input::file-selector-button {
    background-color: #2244ff;
    color: white;
    border-radius: 2px;
    margin: 2px 2px 4px 2px;
    padding: 2px 8px;
    border: none;
}
.Replacer-upload-rulesets > input::file-selector-button:hover {
    background-color: #4477ff;
}
.Replacer-upload-rulesets > div > .upload-button {
    background-color: #2244ff;
    color: white;
    display: inline-block;
    cursor: pointer;
    padding: 2px 8px;
    margin: 3px;
}
.Replacer-upload-rulesets > div > .upload-button:hover {
    background-color: #4477ff;
}
.Replacer-upload-rulesets > div > .upload-button.disabled {
    background-color: #888888;
    cursor: default;
}
.Replacer-upload-rulesets > div > .upload-button.disabled:hover {
    background-color: #888888;
}
.Replacer-upload-rulesets > div > .uploaded-rulesets {
    padding-bottom: 8px;
    display: inline-block;
}
.Replacer-upload-rulesets > div > .uploaded-rulesets > span {
    font-size: small;
    border: 1px solid red;
    border-radius: 2px;
    margin: 2px;
    padding: 2px;
}
.Replacer-upload-rulesets > div > .uploaded-rulesets > span.ok {
    border-color: green;
}

.Ruleset {
    display: flex;
    margin: 2px 4px;
    padding: 4px 4px;
    background: #77ff77;
    border: 1px solid #55dd55;
    border-radius: 3px;
}
.Ruleset-active {
    display: inline-block;
    margin-left: 4px;
    margin-right: 4px;
    cursor: pointer;
}
.Ruleset-name {
    display: inline-block;
    margin-left: 4px;
    margin-right: 4px;
}
.Ruleset-spacer {
    display: inline-block;
    flex-grow: 1;
}
.Ruleset-action-up {
    cursor: pointer;
    display: inline-block;
    margin-left: 4px;
    margin-right: 4px;
    justify-content: end;
}
.Ruleset-action-dn {
    cursor: pointer;
    display: inline-block;
    margin-left: 4px;
    margin-right: 4px;
}
.Ruleset-action-rm {
    cursor: pointer;
    display: inline-block;
    margin-left: 4px;
    margin-right: 4px;
}