.Menu {
    border-bottom: 1px solid gray;
    margin-bottom: 4px;
    display: flex;
}

.Menu-logo {
    color: red;
    font-size: 32px;
    margin-left: 16px;
    margin-right: 16px;
    cursor: default;
}

.Menu-item {
    padding: 8px;
    border-left: 1px dotted transparent;
    border-right: 1px dotted transparent;
    cursor: pointer;
}
.Menu-item:hover {
    background-color: #eeeeee;
}
.Menu-item.active {
    background-color: #ffffdd;
    border-left: 1px dotted #ccaa77;
    border-right: 1px dotted #ccaa77;
}